.main {
    text-align: center;
    padding: 30px 15%;
    margin-top: 50px;
}

.header {
    padding: 0 0 30px 0;
}

.search {
    padding: 15px 0;
}


.mem-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-auto-rows: 10px;
}

.mem-list img {
    width: 250px
}

form {
    text-align: center;
    margin-top: 30px;
}